import { defineComponent, ref, watch, watchEffect } from '@nuxtjs/composition-api';
import moment from 'moment';
export default defineComponent({
    props: {
        wAttr: {
            type: Object,
            default: () => ({})
        },
        defaultValue: {
            type: String,
            default: ''
        }
    },
    setup(props, { emit }) {
        const dateTimeField = ref('');
        const date = ref('');
        const time = ref('');
        const dateModal = ref(false);
        const timeModal = ref(false);
        watchEffect(() => {
            if (props.defaultValue) {
                const dateTime = moment(props.defaultValue, 'YYYY-MM-DD HH:mm', true);
                if (dateTime.isValid()) {
                    date.value = dateTime.format('YYYY-MM-DD');
                    time.value = dateTime.format('HH:mm');
                    dateTimeField.value = dateTime.format('YYYY-MM-DD HH:mm');
                }
            }
        });
        watch(() => [date.value, time.value], () => {
            dateTimeField.value = date.value + ' ' + time.value;
        });
        watch(() => dateTimeField.value, () => {
            if (moment(dateTimeField.value, 'YYYY-MM-DD HH:mm', true).isValid())
                emit('data', dateTimeField.value);
        });
        const setDateTimeNow = () => {
            const dateTimeNow = moment().format('YYYY-MM-DD HH:mm');
            dateTimeField.value = dateTimeNow;
            date.value = moment(dateTimeNow).format('YYYY-MM-DD');
            time.value = moment(dateTimeNow).format('HH:mm');
        };
        const onDateTimeChange = () => {
            if (moment(dateTimeField.value, 'YYYY-MM-DD HH:mm', true).isValid()) {
                date.value = moment(dateTimeField.value).format('YYYY-MM-DD');
                time.value = moment(dateTimeField.value).format('HH:mm');
            }
        };
        return {
            dateTimeField,
            date,
            time,
            setDateTimeNow,
            onDateTimeChange,
            dateModal,
            timeModal
        };
    }
});
